@charset "UTF-8";
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 110px;
    max-width: 1440px;
  }
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-style: normal;
  word-break: break-word;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .page__body {
    grid-template-rows: 1fr auto;
  }
}

@media (min-width: 1440px) {
  .page--inner .page__body {
    grid-template-rows: auto 1fr auto;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.page-header {
  position: relative;
}

@media (min-width: 1440px) {
  .page-header--main-page {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
  }
}

.page-header__main-nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .page-header__main-nav {
    padding: 10px 0 17px;
    min-height: 65px;
  }
}
@media (min-width: 1440px) {
  .page-header__main-nav {
    padding-top: 53px;
  }
}

.page-header__logo {
  margin-right: 0 20px 0 0;
}

.page-header__toggle {
  margin: 12px 0 0;
  padding: 8px 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__main-nav--opened .page-header__toggle {
  position: absolute;
  right: 2px;
  top: 28px;
  z-index: 11;
  margin: -8px 0 0;
  display: block;
  width: 20px;
  height: 20px;
}

.page-header__burger {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
@media (min-width: 768px) {
  .page-header__burger {
    display: none;
  }
}

.page-header__main-nav--opened .page-header__burger {
  width: 25px;
  transform: translate(-2px, 0) rotate(-45deg);
}

.page-header__burger::before {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translate(0, -8px);
}

.page-header__main-nav--opened .page-header__burger::before {
  width: 25px;
  transform: translate(0, 0) rotate(90deg);
}

.page-header__burger::after {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translateY(6px);
}

.page-header__main-nav--opened .page-header__burger::after {
  display: none;
}

.page-header__site-menu {
  margin-left: auto;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .page-header__site-menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header__site-menu {
    margin: 18px 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 35px;
    max-width: 440px;
  }
}
@media (min-width: 1440px) {
  .page-header__site-menu {
    margin: 17px 0 0;
    gap: 34px;
    max-width: 610px;
  }
}

@media (max-width: 767px) {
  .page-header__main-nav--opened .page-header__site-menu {
    position: absolute;
    left: -20px;
    right: -20px;
    top: 65px;
    z-index: 10;
    display: flex;
    flex-direction: column;
  }
}

.page-header__site-menu-link {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .page-header__site-menu-link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1440px) {
  .page-header__site-menu-link {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 768px) {
  .page-header__site-menu-link {
    padding: 7px 0;
  }
}
@media (min-width: 1440px) {
  .page-header__site-menu-link {
    padding: 5px 0;
  }
}

@media (min-width: 1440px) {
  .page-header--main-page .page-header__site-menu-link {
    color: #ffffff;
  }
}

@media (max-width: 767px) {
  .page-header__main-nav--opened .page-header__site-menu-link {
    padding: 22px 0 23px;
    display: block;
    width: 100%;
    background-color: #ffffff;
    border-top: 1px solid #e6e6e6;
  }
}

.page-header__site-menu-link:hover,
.page-header__site-menu-link:focus {
  opacity: 0.6;
}

.page-header__main-nav--opened .page-header__site-menu-link:hover,
.page-header__main-nav--opened .page-header__site-menu-link:focus {
  opacity: 1;
  color: rgba(0, 0, 0, 0.6);
}

.page-header__site-menu-link:active {
  opacity: 0.3;
}

.page-header__main-nav--opened .page-header__site-menu-link:active {
  opacity: 1;
  color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) {
  .page-header__site-menu-link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .page-header__site-menu-link--current {
    border-bottom: 2px solid #68b738;
  }
}

@media (min-width: 1440px) {
  .page-header--main-page .page-header__site-menu-link--current {
    border-color: #ffffff;
  }
}

.logo {
