.button {
  display: block;
  width: 100%;
  padding: 10px 20px;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}

@media (max-width: 767px) {
  .button--promo {
    padding: 11px 20px;
    font-size: 14px;
    line-height: 18px;
  }
}
@media (min-width: 768px) {
  .button--promo {
    padding: 17px 20px;
    width: 244px;
    font-size: 20px;
  }
}

@media (min-width: 768px) {
  .button--limited-size-l {
    padding: 16px 20px;
    width: 282px;
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .button--limited-size-l {
    width: 568px;
  }
}

@media (min-width: 768px) and (max-width: 1439px) {
  .button--limited-size-m {
    margin: 0 38px;
    width: calc(100% - 76px);
  }
}

.button.button--more {
  margin: 0;
  padding: 10px 20px;
  width: 100%;
  color: #444444;
  background-color: #f2f2f2;
}

.button:hover,
.button:focus {
  background-color: #5eaa2f;
}

.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

.button:active.button--disabled {
  color: #ffffff;
}

/*Добавить соответствующей кнопке с классом button класс "button--disabled" для активации стилей неактивной кнопки*/
.button--disabled {
  background-color: #5eaa2f;
  opacity: 0.5;
}

.button--more:hover,
.button--more:focus {
  background-color: #ebebeb;
}

.button--more:active {
  color: #444444;
  background-color: #ebebeb;
  opacity: 0.3;
}

.button--more.button--disabled {
  padding: 10px 20px;
  color: #444444;
  background-color: #f2f2f2;
  opacity: 0.5;
}

.button--more:active.button--disabled {
  color: #444444;
}

